<template>
	<div class="basic-card-container" @click="handleFileClick">
		<el-card class="basic-card" shadow="hover" :key="item.id">
			<template #header>
				<div class="basic-card-header flex justify-between items-center">
					<span class="collect flex items-center cursor-pointer" @click.stop="handleCollect">
						<el-tooltip content="收藏量" placement="top">
							<SvgIcon name="handle-collect" :size="13" />
						</el-tooltip>
						<span class="ml-1">{{ item.collectNum || 0 }}</span>
					</span>
					<span class="view flex items-center cursor-pointer">
            <el-tooltip content="浏览量" placement="top">
						  <SvgIcon name="handle-view" :size="16" />
            </el-tooltip>
						<span class="ml-1">{{ item.hitsNum || 0 }}</span>
					</span>
				</div>
			</template>
			<div class="basic-card-content flex items-center">
				<div class="logo">
          <el-image :src="item.websiteInfo.logo">
            <template #error>
              <SvgIcon name="list-website" :size="40" />
            </template>
          </el-image>
				</div>
				<div class="label relative">
					<h4 class="overflow-hidden">
						<span class="label-name">{{ item.name }}</span>
						<el-tag class="label-tag" type="info" v-for="t in item.tags" :key="t">
							{{ t }}
						</el-tag>
					</h4>
					<p class="overflow-hidden" :title="item.desc">{{ item.desc }}</p>
					<el-icon color="#999" :size="20" class="access" @click.stop="handleDetail"><Right /></el-icon>
				</div>
			</div>
		</el-card>
	</div>
	<SimpleDialog
		v-model:visible="cloneMoveData.visible"
		:title="cloneMoveData.title"
		:footer="false"
	>
		<CloneMove isCollect :itemId="item.id" @formResponse="handleCloneMoveRes" />
	</SimpleDialog>
</template>

<script setup lang="ts">
	import { CardItem } from './types'
	import { Right } from '@element-plus/icons-vue'
	import { useGo } from '@/hooks/web/usePage'
	import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
  import CloneMove from '@/components/ContentElement/clone-move/CloneMove.vue'
  import {useCloneMoveForm} from '@/components/ContentElement/clone-move/useCloneMove'
  import {fetchHit} from '@/api/fetchs/nav'
  import {getSearchParams} from "@/utils/form";
  import {useEmit} from "@/utils/useEmitter";

	const props = defineProps<{
		item: CardItem
	}>()
	const go = useGo()

	const { cloneMoveData, handleCloneMoveVisible, handleCloneMoveRes } = useCloneMoveForm('收藏到')

	async function handleCollect() {
		handleCloneMoveVisible()
	}

	async function handleDetail() {
    await handleHitApi()

		go('/resource-navigation/website-detail/' + props.item.id)
	}

  // 点击
  async function handleFileClick(){
    if(props.item.dataType == 2){
      window.open(props.item.websiteInfo.url)
    }
    await handleHitApi()
  }

  async function handleHitApi() {
    const params = getSearchParams({id:props.item.id})
    await fetchHit(params)
    useEmit('handleUpdateList')
  }
</script>

